import React from "react";
import {
  FiFacebook,
  FiTwitter,
  FiInstagram,
  FiDribbble,
  FiLinkedin,
  FiGithub
} from "react-icons/fi";

const SocialShare = [
  {
    iconName: <FiGithub />,
    link: "https://www.github.com/reecehumphreys/",
  },
  {
    iconName: <FiLinkedin />,
    link: "https://www.linkedin.com/feed/",
  },
  { iconName: <FiTwitter />, link: "https://twitter.com/ReeceWHumphreys" },
  //{
  //  iconName: <FiInstagram />,
  //  link: "https://www.instagram.com/reecewhumphreys/",
  //},
  //{ iconName: <FiDribbble />, link: "https://dribbble.com/" },

];
const Social = () => {
  return (
    <ul>
      {SocialShare.map((val, i) => (
        <li key={i}>
          <a target="blank" href={val.link} target="_blank" rel="noreferrer">
            {val.iconName}
          </a>
        </li>
      ))}
    </ul>
  );
};

export default Social;
