import React, { useState } from "react";
import Skills from "../skills/Skills";
import Modal from "react-modal";
import ModalContent from "./modal/ModalContent";

Modal.setAppElement("#root");

const About = () => {
  const [isOpen, setIsOpen] = useState(false);
  function toggleModalOne() {
    setIsOpen(!isOpen);
  }

  return (
    //    ABOUT
    <div className="edina_tm_about" id="about">
      <div className="container">
        <div className="about_title">
          <h3>About Me</h3>
        </div>
        <div className="content">
          <div
            className="leftpart"
            data-aos="fade-right"
            data-aos-duration="1200"
            data-aos-delay="100"
          >
            <div className="info">
              <h3>
                 Hi, I'm <span>Reece Humphreys</span>
              </h3>
              <p>
              I was fortunate enough to grow up in central Florida, where I frequently visited the space coast to watch Shuttle launches and taught myself to code from a young age. I attended the Harriet L. Wilkes Honors College of Florida Atlantic University to pursue mathematics and physics while getting a minor in data science. Specifically, I combined my programming knowledge with a more formal education in the sciences to tackle complex engineering problems. For example, I wrote a thesis on the applications of Lie groups and Lie Algebras in computing and control theory problems. Additionally, I wrote a physics thesis about simulating orbital debris cloud formation, which won an outstanding thesis award.
              </p>
            </div>
            <div className="edina_tm_button">
              <button
                type="submit"
                className="color"
                onClick={toggleModalOne}
              >
                More About Me
              </button>
            </div>
            </div>
            {/*
            <div className="my_skills">
              <h3 className="title">Contact Details</h3>
              <p className="desc">
                Email: <a href = "mailto: reecewh@icloud.com">reecewh@icloud.com</a>

              </p>
              <div className="wrapper">
                <div className="dodo_progress">
                  <Skills />
                </div>
              </div>
            </div>
          </div>
          */}

          <div className="rightpart">
            <div className="image">
              <img src="img/thumbs/26-35.jpg" alt="thumb" />

              <div
                className="main"
                style={{
                  backgroundImage: `url(${
                    process.env.PUBLIC_URL + "img/about/2.jpeg"
                  })`,
                }}
                data-aos="fade-left"
                data-aos-duration="1200"
                data-aos-delay="200"
              >
              </div>
            </div>
          </div>
          {/* End righttpart */}
        </div>
      </div>

      {/* Start About Details Modal */}
      <Modal
        isOpen={isOpen}
        onRequestClose={toggleModalOne}
        contentLabel="My dialog"
        className="custom-modal about-popup-wrapper"
        overlayClassName="custom-overlay "
        closeTimeoutMS={500}
      >
        <div className="edina_tm_modalbox">
          <button className="close-modal" onClick={toggleModalOne}>
            <img src="/img/svg/cancel.svg" alt="close icon" />
          </button>
          {/* End close icon */}

          <div className="box_inner">
            <div className="description_wrap scrollable">
              <ModalContent />
            </div>
          </div>
          {/* End box inner */}
        </div>
        {/* End modal box news */}
      </Modal>
      {/* End  About Details Modal */}
    </div>
    // /ABOUT
  );
};

export default About;
